.project {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 30%;
	height: 390px;
	margin-top: 15px;
	margin-bottom: 50px;
	background-color: rgba(0, 0, 0, 0.116);
	padding: 20px;
	border-radius: 50px;
	backdrop-filter: blur(20px);
}

.project .project-img {
	width: 100%;
	height: 180px;
	border-radius: 40px;
	border: 2px solid var(--color-sec-light);
	transition: 0.5s ease;
	object-fit: cover;
	object-position: left;
}

.project:hover > .project-img {
	object-position: center;
}

.project .project-info {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 155px;
}

.project .project-info .name {
	font-size: 22px;
	font-weight: 500;
}

.project .project-info .position {
	font-size: 12px;
	font-weight: 400;
	margin: 5px 0 30px 0;
	letter-spacing: 2px;
}

.project .project-info .contact-project {
	align-self: flex-start;
	background-color: var(--color-sec-light);
	padding: 17px 35px;
	font-size: 18px;
	border-radius: 50px;
	transition: 0.1s ease;
}

.project .project-info .contact-project:hover {
	background-color: transparent;
	outline: 2px solid var(--color-sec-light);
}

@media (min-width: 1920px) {
	.project {
		height: 580px;
		padding: 30px !important;
	}

	.project .project-info {
		height: 100%;
		padding: 20px 0;
	}

	.project .project-img {
		width: 100%;
		height: 450px;
	}

	.project .project-info .name {
		font-size: 35px !important;
	}
	.project .project-info .position {
		font-size: 18px !important;
	}
	.project .project-info a.contact-project {
		font-size: 28px !important;
		margin-top: 40px;
	}
}

@media (min-width: 1440px) {
	h2 {
		font-size: 35px;
	}
}

@media (max-width: 820px) {
	.project {
		flex-direction: column;
		margin-bottom: 35px;
		padding: 40px;
		width: 100%;
		height: initial;
	}

	.project .project-img {
		width: 100%;
		height: 330px;
		border-radius: 40px 40px 0 0;
		object-position: center;
	}

	.project .project-info {
		max-width: 100%;
		padding: 20px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		height: initial;
	}

	.project .project-info .name {
		font-size: 45px;
		font-weight: 500;
		letter-spacing: 2px;
		margin-bottom: 5px;
	}

	.project .project-info .position {
		font-size: 25px;
		font-weight: 200;
		letter-spacing: 2px;
		margin-bottom: 10px;
	}

	.project .project-info a.contact-project {
		background-color: var(--color-sec-light);
		padding: 15px 25px;
		font-size: 28px;
		border-radius: 50px;
		transition: 0.1s ease;
		margin-top: 40px;
	}
}

@media (max-width: 500px) {
	.project {
		width: 100%;
		margin-top: 15px;
		margin-bottom: 25px;
		padding: 15px;
	}

	.project .project-img {
		height: 200px;
