		letter-spacing: 2px;
	}
	.project .project-info .position {
		font-size: 12px;
		font-weight: 200;
		letter-spacing: 2px;
	}
	.project .project-info .about {
		font-weight: 100;
		font-size: 16px;
	}
	.project .project-info a.contact-project {
		background-color: var(--color-sec-light);
		padding: 15px 25px;
		font-size: 15px;
		border-radius: 50px;
		transition: 0.1s ease;
		margin-top: 30px;
	}
}

