.header-container {
	min-height: 100vh;
	padding: 0;
	margin: 0;
	width: 100%;
	text-align: center;
	opacity: 0;
	transform: translateY(-100px);
	backdrop-filter: blur(20px);
	animation: fade-in 1s ease 1 forwards;
}

.header-container .title-container {
	position: absolute;
	top: 30px;
}

.header-container .title-container h1 {
	display: flex;
	flex-direction: column;
	font-size: 28px;
	text-transform: uppercase;
	font-weight: 900;
	letter-spacing: 2px;
}

.header-container .title-container h1 p {
	font-size: 15px;
}

.header-container .title-container h1 p span {
	color: var(--color-pri);
	font-size: 20px;
}

.header-container .title-container h2 {
	font-size: 16px;
	font-weight: 100;
	color: var(--color-pri);
	letter-spacing: 1px;
	border: none;
}

.header-container .title-container h2:after {
	display: none;
}

.header-container .logo {
	width: 300px;
	height: 300px;
	border-radius: 50%;
	transform: translate(-200px);
	opacity: 0;
	margin-top: 50px;
	border-radius: 50%;
	border: 3px solid rgb(108, 161, 197);
	background: url(../../assets/alan-images/alan.jpg);
	background-size: cover;
	background-position: center;
	animation: fade-in 1s ease 1 0.5s forwards;
}

.scroll-down {
	transform: rotate(90deg) translateX(-85px);
	position: absolute;
	right: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	user-select: none;
	gap: 10px;
}

.scroll-down hr {
	background-color: blue;
	width: 20px;
}

.scroll-down h5 {
	font-size: 18px;
	text-transform: capitalize;
	font-weight: 100;
}

.scroll-down .scroll {
	transform: rotate(-90deg);
	font-size: 20px;
}

@keyframes fade-in {
	to {
		transform: translate(0);
		opacity: 1;
	}
}

